@mixin style-text {
  margin-top: 15px;
  margin-right: 80px;
  font-size: 20px;
}
@mixin button-hover {
  text-decoration: none;
  top: 2rem;
  padding: 8px 22px;
  position: relative;
  border-radius: 20px;
  border: none;
  background: $white2;
  color: $blue1;
  &::after {
    content: "";
    position: absolute;
    width: 80%;
    transform: scaleX(0);
    height: 3px;
    bottom: 8px;
    left: 10%;
    background-color: $blue1;
    transform-origin: bottom;
    transition: transform 0.6s cubic-bezier(0.53, 0.21, 0, 1);
  }
  &:hover::after {
    transform: scaleX(1);
    transform-origin: bottom right;
  }
}
.ImageSection {
  display: flex;
  @media (max-width: 40em) {
    display: block;
  }
  @media (min-width: 40.0625em) and (max-width: 60em) {
    margin-top: 10px;
  }
  img {
    margin: 2rem 3rem;
    width: 26%;
    height: auto;
    border-radius: 20px;
    @media (min-width: 64.0625em) and (max-width: 80em) {
      margin: 40px;
    }
    @media (min-width: 60.0625em) {
      margin: 5rem 3rem 3rem 3rem;
    }
    @media (min-width: 40.0625em) and (max-width: 60em) {
      margin: 5rem 0rem 3rem 2rem;
      width: 36%;
    }
    @media (min-width: 30.0625em) and (max-width: 40em) {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 50%;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 50%;
    }
  }
  .about-info {
    padding-top: 20px;
    @media (min-width: 30.0625em) and (max-width: 40em) {
      padding-top: 70px;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      padding-top: 50px;
    }
  }
  .text-lower1 {
    @include style-text;
    padding-top: 2rem;
    color: $black1;
  }
  .text-lower2 {
    @include style-text;
    color: $black1;
  }
  .text-lower3 {
    @include style-text;
    color: $black1;
  }
  .text-lower1,
  .text-lower2,
  .text-lower3 {
    @media (min-width: 64.0625em) and (max-width: 80em) {
      margin-right: 40px;
    }
    @media (min-width: 40.0625em) and (max-width: 64em) {
      margin: 1.5rem 3rem;
    }
    @media (max-width: 40em) {
      margin: 0;
      padding: 10px 20px;
    }
  }

  .link-cv {
    margin-left: 1.5rem;
    @include button-hover;
    border: 1px solid $brown1;
    @media (max-width: 40em) {
      display: inline-block;
      left: 50%;
      margin: 1rem auto;
    }
    @media (min-width: 40.0625em) and (max-width: 60em) {
      margin-left: 70px;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      left: 30%;
    }
  }
}
