.PortfolioPage {
  .title-portfolio {
    @include focus-expand;
    padding: 2rem 3rem 0 15rem;
    @media (min-width: 20em) and (max-width: 30em) {
      padding-left: 30px;
    }
    @media (min-width: 30.0625em) and (max-width: 40em) {
      padding-left: 60px;
    }
  }
  .portfolist {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem 0;
    padding: 1rem;
    // margin-left: 1rem;
    align-items: center;
    justify-items: center;
    @media (min-width: 60.0625em) and (max-width: 80em) {
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      justify-items: center;
    }
    @media (min-width: 40.0625em) and (max-width: 60em) {
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      justify-items: center;
      margin: 0;
      padding: 1rem 0.5rem;
    }
    @media (min-width: 30.0625em) and (max-width: 40em) {
      grid-template-columns: 1fr;
      align-items: center;
      justify-items: center;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      padding: 0;
      margin: 0;
      grid-template-columns: 1fr;
      align-items: center;
      justify-items: center;
    }
  }
  .portfolio {
    h5 {
      display: inline-block;
      font-size: 1.7rem;
      font-weight: 400;
      color: #fff;
      transition: all 0.2s ease-in-out;
      &:hover {
        color: #000;
        cursor: pointer;
      }
    }
    @media (min-width: 20em) and (max-width: 30em) {
      margin: 0;
    }
  }
  .image-data {
    width: 330px;
    height: 230px;
    border-radius: 8px;
    position: relative;
    @media (min-width: 20em) and (max-width: 30em) {
      width: 300px;
    }
    @media (min-width: 40.0625em) and (max-width: 60em) {
      width: 300px;
    }
    &::before {
      content: "";
      position: absolute;
      width: 330px;
      height: 230px;
      border-radius: 8px;
      background-color: rgba(231, 142, 142, 0.234);
      transform-origin: left;
      transform: scale(0);
      transition: all 0.4s ease-in-out;
    }
    &:hover::before {
      transform: scale(1);
    }
    img {
      border-radius: 8px;
      width: 330px;
      height: 230px;
      @media (min-width: 20em) and (max-width: 30em) {
        width: 300px;
      }
      @media (min-width: 40.0625em) and (max-width: 60em) {
        width: 300px;
      }
    }
    .hover-items {
      list-style: none;
      position: absolute;
      top: 40%;
      left: 12%;
      display: flex;
    }
  }
  li {
    margin-left: 15px;
    padding: 12px 15px;
    border: 2px solid $pink2;
    &:hover {
      a {
        color: $white1;
      }
    }
    a {
      text-decoration: none;
      font-size: 20px;
      font-weight: 500;
      color: $pink2;
    }
  }
  .buttons-seclect {
    padding: 1rem 4rem 4rem 4rem;
    @media (min-width: 20em) and (max-width: 30em) {
      margin: 0;
      padding: 1rem 0.5rem 4rem 0.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    @media (min-width: 30.0625em) and (max-width: 40em) {
      margin: 0;
      padding: 1rem 0.5rem 4rem 0.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .btn-port {
    @include button-hover;
    margin: 0 2rem;
    padding-left: 20px;
    font-size: 22px;
    border: 1px solid $blue1;
    border-radius: 8px;
    background-color: $white5;
    &::after {
      width: 70%;
      left: 15%;
      bottom: 6px;
    }
    &:hover {
      background-color: $white3;
    }
    @media (min-width: 40.0625em) {
      left: 15%;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      margin: 0 auto;
    }
  }
}
