.skills-container {
  margin-bottom: 4rem;
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
  justify-items: center;
  @media (max-width: 51.25em) {
    grid-template-columns: 1fr;
  }

  .skill-item-container {
    margin: auto;
    padding: 5px 5px;
    width: 400px;
    height: 170px;
    justify-items: center;
    text-align: center;
    @media (min-width: 20em) and (max-width: 30em) {
      width: 320px;
    }
  }
  .skill-name {
    h4 {
      display: block;
      font-size: 1.5rem;
      font-weight: normal;
      padding: 0.5rem 0.5rem;
      @media (min-width: 20em) and (max-width: 30em) {
        font-size: 20px;
        padding: 8px 5px;
      }
    }
  }
  .logo-skill {
    img {
      margin: 15px;
      padding: 2px;
      width: 70px;
      height: 70px;
    }
  }
}
