.App {
  .sidebar {
    width: 12em;
    height: 100vh;
    background-color: $blue2;
    border: 2px solid $white1;
    border-radius: 2rem 0;
    position: fixed;
    z-index: 11;
    @media screen and (max-width: 64em) {
      width: 10em;
      transform: translateX(-100%);
      transition: all 0.6s ease-in-out;
    }
  }
  .nav-toggle {
    @media screen and (max-width: 64em) {
      transform: translateX(0);
      transition: all 0.6s ease-in-out;
    }
  }

  .main-content {
    width: calc(100% - 12em);
    margin-left: 12em;
    min-height: 100vh;
    background-color: $brown3;
    display: grid;
    @media screen and (max-width: 64em) {
      margin-left: 0;
      width: 100%;
      .HomePage {
        padding: 0;
      }
    }
  }
  background-size: 7.5rem 8rem;
  background-image: url("../img/repeat\ img\ bg.jpg");
  background-repeat: repeat round;
}
