.NavBar {
  height: 100vh;

  .nav {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    .profile {
      width: 100%;
      text-align: center;
      margin-top: 2.5rem;
      border: 50%;
    }
    img {
      width: 65%;
      border-radius: 50%;
      border: 3px solid;
      border-color: $brown2;
      margin-bottom: 1rem;
    }
    .nav-items {
      width: 76%;
    }
    .nav-item {
      margin: 14px;
      list-style: none;
      text-align: center;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 0.4rem;
        height: 100%;
        background-color:$blue1;
        transform-origin: bottom;
        transform: scale(0);
        // overflow-x: hidden;
        transition: transform 0.3s,
          0.2s width 0.5s cubic-bezier(1, -0.16, 0.6, 1.35);
      }
      &:hover::before {
        width: 100%;
        transform: scale(1);
      }
    }
    a {
      z-index: 10;
      text-decoration: none;
      // font-size: inherit;
      font-size: 1rem;
      font-weight: 500;
      color: black;
      display: block;
      padding: 0.5rem 0;
      position: relative;
    }
    .footer {
      width: 100%;
      border-top: 2px solid $white2;
      p {
        font-size: 1rem;
        text-align: center;
        padding: 1rem 0;
      }
    }
  }
}
.active {
  background-color: rgba(243, 145, 145, 0.61);
}
