.nav-btn {
  position: absolute;
  top: 5%;
  right: 8%;
  width: 60px;
  height: 60px;
  background-color: $white5;
  border: 1.5px solid $brown1;
  border-radius: 6px;
  display: none;
  z-index: 20;
  &:hover {
    cursor: pointer;
    background-color: $white3;
  }
  @media screen and (max-width: 64em) {
    display: block;
  }
  @media (min-width: 20em) and (max-width: 30em){
    right: 8%;
  }
  .line {
    width: $widthline;
    height: 6px;
    background-color: $brown1;
    border-radius: 6px;
    margin: 10px auto;
  }
  .line2 {
    margin-left: 5px;
    width: calc(#{$widthline} * 0.8);
  }
  .line3 {
    margin-left: 5px;
    width: calc(#{$widthline} * 0.6);
  }
}
