.BlogsPage {
  .title-blogs {
    @include focus-expand;
    padding: 4rem 0 2rem 15rem;
    @media (min-width: 20em) and (max-width: 30em) {
      padding: 30px;
    }
    @media (min-width: 30.0625em) and (max-width: 40em) {
      padding: 30px 0 30px 20%;
    }
    @media (min-width: 40.0625em) and (max-width: 60em) {
      padding: 30px 0 30px 20%;
    }
  }
  .gird-blogs {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 4rem 0;
    padding: 2rem 1rem 3rem 1rem;
    margin-left: 3.5rem;
    @media (min-width: 64.0625em) and (max-width: 80em) {
      grid-template-columns: 1fr;
      align-items: center;
      justify-items: center;
    }
    @media (min-width: 60.0625em) {
      margin-left: 0;
      align-items: center;
      justify-items: center;
    }
    @media (max-width: 60em) {
      margin-left: 0;
      grid-template-columns: 1fr;
      align-items: center;
      justify-items: center;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      padding-left: 0;
      padding-right: 0;
    }
  }
  .blog-product {
    height: auto;
    width: 430px;
    border-radius: 6px;
    background-color: $white1;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
    overflow: hidden;
    &:hover {
      background-color: $blue5;
      .product-image {
        transform: scale(1.07);
        transition: 0.3s;
      }
      .product-title-icons {
        color: $blue4;
      }
      .product-icon--view,
      .product-icon--like {
        color: $blue4;
      }
    }
    @media (min-width: 20em) and (max-width: 30em) {
      width: 310px;
    }
  }
  .blog-product--link {
    text-decoration: none;
  }
  .product-container-content {
    width: 100%;
    padding: 8px 8px;
    .product-image {
      border-radius: 10px;
      background-color: rgba(255, 255, 255, 0.556);
      width: 100%;
      height: 276px;
      @media (min-width: 20em) and (max-width: 30em) {
        height: 200px;
      }
    }
  }
  .product-title-icons {
    color: $black1;
    display: flex;
    padding: 5px;
    border: 1.5px solid $brown3;
    border-radius:4px;
    .product-title {
      text-decoration: none;
      width: 250px;
      display: flex;
      word-wrap: break-word;
      overflow: hidden;
      display: -webkit-box;
      text-overflow: ellipsis;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
    }
  }
  .product-icons {
    padding-left: 15px;
    @media (min-width: 20em) and (max-width: 30em) {
      padding-left: 50px;
    }
    .product-icon {
      width: 16px;
      height: 16px;
      &:hover {
        path {
          fill: $brown1;
        }
      }
    }
    .product-icon--view,
    .product-icon--like {
      padding: 6px 10px 6px 3px;
    }
  }
}
