.ContactPage {
  .title-contact {
    @include focus-expand;
    padding: 2.5rem 7rem 3rem 15rem;
    @media (max-width: 80em) {
      padding-bottom: 4rem;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      padding: 30px;
    }
    @media (min-width: 30.0625em) and (max-width: 40em) {
      padding: 50px;
    }
  }
  .map-contact-sect {
    display: flex;
    margin: 0 3rem;
    @media (max-width: 80em) {
      display: block;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      margin: 0;
      padding: 0;
    }
    @media (min-width: 30.0625em) and (max-width: 40em) {
      margin: 0;
      padding: 0;
    }
  }
  .map-sect {
    // padding-left: 5rem;
    iframe {
      border: 0;
      width: 440px;
      height: 440px;
      @media (min-width: 20em) and (max-width: 30em) {
        padding-top: 20px;
        width: 280px;
        height: 280px;
      }
    }
    @media (max-width: 80em) {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0;
    }
  }
  .contact-sect {
    padding-left: 1rem;
    img {
      margin: 1rem;
      padding: 1rem;
      border: 2px solid $brown1;
      border-radius: 6px;
    }
    @media (min-width: 20em) and (max-width: 30em) {
      padding: 0;
      img {
        padding: 0.5rem;
        margin: 10px;
        display: inline-block;
        align-self: center;
      }
    }
    @media (min-width: 30.0625em) and (max-width: 40em) {
      padding: 0;
      
    }
  }
}
.ContactItem {
  padding: 0 1rem 1rem 2.5rem;
  @media (max-width: 80em) {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem 0 0 0;
    &:last-child {
      padding-bottom: 5rem;
    }
  }
  .contact {
    border: 1px solid $brown1;
    border-radius: 10px;
    display: flex;
    width: 450px;
    height: 140px;
    background-color: $white2;
    @media (min-width: 20em) and (max-width: 30em) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 300px;
      height: 250px;
    }
  }
  .right-item {
    align-items: center;
    padding: 2rem 0 1rem;
    @media (min-width: 20em) and (max-width: 30em) {
      display: block;
      padding: 0 0 0 1rem;
    }
    h6 {
      padding-left: 1rem;
      font-size: 16px;
    }
    p {
      padding: 3px 0;
      font-size: 16px;
    }
  }
}
