.HomePage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  padding-left: 18%;
}
.hero-text {
  color: white;
  text-align: center;
  font-size: 3.6rem;
  @media (min-width: 20em) and (max-width: 30em) {
    font-size: 2.5rem;
  }

  .deverloper {
    color: #66bef1;
  }
  .uidesigner {
    color: #ab6e6f;
  }
  .sub-text {
    font-size: 1rem;
    padding-top: 2rem;
    @media (min-width: 20em) and (max-width: 30em) {
      font-size: 12px;
    }
  }
}
.icons {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0;

  @media (max-width: 64.0625em) {
    .fb {
      color: #4064ac;
      &:hover {
        opacity: 0.8;
      }
    }
    .gh {
      color: #302f2f;
      &:hover {
        opacity: 0.8;
      }
    }
    .yt {
      color: #d82825;
      &:hover {
        opacity: 0.8;
      }
    }
  }
  .icon-holder {
    margin-right: 3rem;
    color: #66bef1;
    @media (min-width: 20em) and (max-width: 30em) {
      margin: 0 auto;
    }
    .fb:hover {
      color: #4064ac;
      transition: all 0.8s;
    }
    .gh:hover {
      color: #302f2f;
      transition: all 0.8s;
    }
    .yt:hover {
      color: #d82825;
      transition: all 0.8s;
    }
  }
}
