* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  color: $black1;
  font-family: "Poppins", sans-serif;
  font-size: 1.2rem;
  &::-webkit-scrollbar {
    position: absolute;
    width: 10px;
  }
  &::-webkit-scrollbar-track-piece {
    background-color: $brown2;
  }
  &::-webkit-scrollbar-thumb {
    -webkit-border-radius: 8px;
    border-radius: 8px;
    background:$white2;
  }
}

