@mixin focus-expand {
  display: inline-block;
  font-size: 2.2rem;
  font-weight: 400;
  padding-left: 24rem;
  color: black;
  -webkit-animation: focus-in-expand 3s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite alternate;
  animation: focus-in-expand 3s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite alternate;
  @-webkit-keyframes focus-in-expand {
    0% {
      letter-spacing: -0.5em;
      -webkit-filter: blur(12px);
      filter: blur(12px);
      opacity: 0;
    }
    100% {
      -webkit-filter: blur(0px);
      filter: blur(0px);
      opacity: 1;
    }
  }
  @keyframes focus-in-expand {
    0% {
      letter-spacing: -0.5em;
      -webkit-filter: blur(12px);
      filter: blur(12px);
      opacity: 0;
    }
    100% {
      -webkit-filter: blur(0px);
      filter: blur(0px);
      opacity: 1;
    }
  }
}

.title {
  position: relative;
  font-size: 3.5rem;
  top: 30px;
  padding: 4rem 0 2rem 2rem;
  color: #ffffffc7;
  @media (min-width: 20em) and (max-width: 30em) {
    padding-left: 20px;
    font-size: 2.5rem;
  }
  &::after {
    position: absolute;
    content: "";
    width: 90%;
    height: 0.4rem;
    bottom: 20%;
    left: 2rem;
    right: 0;
    border-radius: 10px;
    background-color: #66bef1;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);
  }
  &:hover::after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
}
.text-bigger {
  display: block;
  font-size: 4.5rem;
  color: #66bef1c9;
  text-transform: uppercase;
  padding: 20px 0 0 35%;
  @media (min-width: 64.0625em) and (max-width: 80em) {
    padding-left: 15%;
  }
  @media (min-width: 40.0625em) {
    padding-left: 15%;
  }
  @media (min-width: 30.0625em) {
    font-size: 2.5rem;
    padding-left: 20%;
  }
  @media (min-width: 20em) and (max-width: 30em) {
    padding-left: 30px;
    padding-top: 30px;
    font-size: 2rem;
  }
}
.title-aboutme {
  @include focus-expand;
  position: absolute;
  @media (min-width: 40.0625em) and (max-width: 60em) {
    display: block;
    padding-left: 26%;
  }
  @media (min-width: 30.0625em) and (max-width: 40em) {
    top: 550px;
    padding-left: 48px;
    position: static;
  }
  @media (min-width: 20em) and (max-width: 30em) {
    top: 350px;
    padding-left: 120px;
    position: static;
  }
}
.title-about {
  @include focus-expand;
  padding: 2rem 3rem;
  @media (max-width: 40.0625em) {
    display: block;
  }
}
